* {
  box-sizing: border-box;
}

html {
  font-family: "Playfair", serif;
}

.page-header_container,
.container {
  position: relative;
  width: 100%;
  max-width: 1230px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}

img {
  width: 100%;
  max-width: 100%;
}

.primary-button {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  line-height: 21.33px;
  letter-spacing: 0.02em;
  background-color: #1C4648;
  color: #D5D46F;
}

.main-menu_nav_list {
  padding-left: 0;
  list-style: none;
}
.main-menu_nav_list li {
  padding: 0;
  list-style: none;
}

.mobile-menu-checker {
  display: none;
}
.mobile-menu-btn {
  position: absolute;
  right: 15px;
  top: 0;
  width: 40px;
  height: 40px;
  z-index: 5;
  cursor: pointer;
}
.mobile-menu-btn span {
  display: block;
  position: relative;
  width: 40px;
  height: 5px;
  margin-top: 17.5px;
  background-color: #1C4648;
}
.mobile-menu-btn span::before {
  top: -10px;
  right: 0;
}
.mobile-menu-btn span::after {
  top: 10px;
  right: 0;
}
.mobile-menu-btn span::after, .mobile-menu-btn span::before {
  content: "";
  display: block;
  position: absolute;
  width: 40px;
  height: 5px;
  background-color: #1C4648;
}

#page-header {
  padding-top: 15px;
  padding-bottom: 15px;
}

.page-header_container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.page-header_container_logo {
  max-width: 150px;
}
.page-header_container_nav {
  display: none;
}
.page-header_container_nav a {
  color: #111111;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  line-height: 21.33px;
  letter-spacing: 0.02em;
}

@media screen and (min-width: 768px) {
  #page-header {
    padding-top: 40px;
  }

  .page-header_container_logo {
    max-width: 195px;
  }
  .page-header_container_nav {
    display: flex;
  }
}